import { defineComponent } from 'vue';
import prefix, { PREFIX } from '../../mixins/prefix';
import { Icon } from 'gw-vue-ui';
export default defineComponent({
    name: `${PREFIX}-button`,
    mixins: [prefix],
    components: {
        Icon
    },
    props: {
        type: {
            type: String,
            default: '',
            validator: (val) => {
                return [
                    'default',
                    'primary',
                    'ghost'
                ].includes(val);
            }
        },
        size: {
            type: String,
            default: '',
            validator: (val) => {
                return [
                    'normal',
                    'mini',
                    'small',
                    'large'
                ].includes(val);
            }
        },
        disabled: {
            type: Boolean,
            default: false
        },
        loading: {
            type: Boolean,
            default: false
        },
        icon: {
            type: String,
            default: ''
        },
        round: {
            type: Boolean,
            default: false
        },
        customStyle: {
            type: String,
            default: ''
        }
    },
    methods: {
        handleClick(e) {
            this.$emit('click', e);
        }
    }
});
