import { defineComponent, computed } from 'vue';
// import { isDef } from '../../utils'
import prefix, { PREFIX } from '../../mixins/prefix';
// Composition
import { useRoute, routeProps } from '../../utils/useRoute';
import { useParent } from '../../utils/use/useParent';
const name = `${PREFIX}-row`;
export const colProps = {
    offset: [Number, String],
    tag: String
};
export default defineComponent({
    name: `${PREFIX}-col`,
    mixins: [prefix],
    props: {
        span: [Number, String],
        ...colProps,
        ...routeProps
    },
    data() {
        return {};
    },
    setup() {
        const { parent, index } = useParent(name);
        console.log('parent', parent);
        const style = computed(() => {
            if (!parent) {
                return {};
            }
            const { spaces } = parent;
            if (spaces && spaces.value && spaces.value[index.value]) {
                const { left, right } = spaces.value[index.value];
                console.log('left', left, right);
                return {
                    paddingLeft: left ? `${left}px` : null,
                    paddingRight: right ? `${right}px` : null
                };
            }
            return {};
        });
        console.log('style+', style);
        const route = useRoute();
        return { style, route };
    }
});
